import React from 'react';
import './RightColumn.css';
import Column from './Column';
import { connect } from 'react-redux';
import { updateRemovedLocations } from '../../../modules/locations'; //   action -  

// props     props.removedLocations,    props
const RightColumn = ({ removedLocations, onChange }) => {
  return (
      <div className="right-column">
        <div> </div>
        <div className="delet-locations-columns">
          <Column removedLocations={removedLocations} onChange={onChange} />
        </div>
      </div>

  );
}

const mapStateToProps = ({ locations }) => ({ 
  //    .    locations,    removedLocations.
  //      props   removedLocations
  removedLocations: locations.removedLocations,
})

const mapDispatchToProps = {
  //     action
  //     mapDispatchToProps
  //       
  onChange: updateRemovedLocations,
};


export default connect( //      
  mapStateToProps,
  mapDispatchToProps,
)(RightColumn);